.card {
    height: 30vh;
    width: 100vw;
    /* background-color: rgba(24, 24, 24, 0.055); */
    margin: 8.5px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* hyphens: auto; */
    transition: all 1s;
    font-size: 1.2em;
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
}

.card:hover {
    transform: scale(1.05);
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.123);
    transition: all 1s;
}

@media screen and (min-width: 480px) {
    .card {
        height: 26vh;
        width: 40vw;
        border-radius: 10px;
        font-size: 1.5em;
    }
}

@media screen and (min-width: 720px) {
    .card {
        height: 25vw;
        width: 25vw;
    }
}

