.headertext {
    background: rgb(0, 0, 0);
    letter-spacing: 7px;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Staatliches', cursive;
    font-size: 6em;
    text-transform: uppercase;
    text-shadow: 4px 3px 0px white;
}



